import { FormattedMessage } from "react-intl";
import { Link } from "react-router-dom";
import { Badge } from "reactstrap";

const statusObj = {
  1: {
    class: "light-success",
    text: <FormattedMessage id="Active" />,
  },
  // 0: {
  //   class: "light-warning",
  //   text: <FormattedMessage id="Deactive" />,
  // },
  0: {
    class: "light-secondary",

    text: <FormattedMessage id="Blocked" />,
  },
};

export const columns = [
  {
    name: <FormattedMessage id="student_code" />,
    maxWidth: "150px",
    selector: "name",
    cell: (row) => row.id && row.id,
  },
  {
    name: <FormattedMessage id="nameLearn" />,
    minWidth: "120px",
    selector: "nameLearn",
    cell: (row) => {
      if (row?.user?.first_name && row?.user?.last_name) {
        return row?.user?.first_name + " " + row?.user?.last_name;
      } else {
        return "";
      }
    },
  },
  {
    name: <FormattedMessage id="Course" />,
    minWidth: "120px",
    selector: "Course",
    cell: (row) => {
      if (row?.product?.product_names[0]?.name) {
        return row?.product?.product_names[0]?.name;
      } else {
        return "";
      }
    },
  },
  {
    name: <FormattedMessage id="Certificate" />,
    minWidth: "120px",
    selector: "Certificate",
    cell: (row) => {
      if (row?.certificate?.title) {
        return row?.certificate?.title;
      } else {
        return "";
      }
    },
  },
  {
    name: <FormattedMessage id="status" />,
    minWidth: "170px",
    selector: "status",
    cell: (row) => {
      if (row.status) {
        return (
          <Badge
            className="text-capitalize"
            color={statusObj[row.status]?.class}
          >
            {statusObj[row.status]?.text}
          </Badge>
        );
      } else {
        return (
          <Badge className="text-capitalize" color={statusObj[0]?.class}>
            {statusObj[0]?.text}
          </Badge>
        );
      }
    },
  },
  {
    name: <FormattedMessage id="action" />,
    minWidth: "100px",

    cell: (row) => {
      return (
        row?.id && (
          <div className="mh-100 d-flex align-items-center justify-content-center mw-100">
            <Badge
              className=" w-100"
              pill
              color="primary"
              style={{ padding: "10px 25px" }}
            >
              <Link
                className="text-edit hover:text-white"
                to={`/apps/achievements/certificate/edit/${row.id}`}
              >
                {" "}
                <FormattedMessage id="edit" />
              </Link>
            </Badge>
          </div>
        )
      );
    },
  },
];
