import Sidebar from "@components/sidebar";
import { isObjEmpty } from "@utils";
import classnames from "classnames";
import { useEffect, useState } from "react";
import { Controller, useForm } from "react-hook-form";
import { FormattedMessage, injectIntl } from "react-intl";
import "react-phone-number-input/style.css";
import { useDispatch, useSelector } from "react-redux";
import { Button, Form, FormGroup, Input, Label, Media, Row } from "reactstrap";
import validateOptions from "@constants/validate";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { toast } from "react-toastify";
import { BG_BLANK } from "./../../../../../constants/app";
import { convertFileToBase64, uploadImage } from "../../../../../helper/common";
import Select from "react-select";
import { addUserCertificate, getById } from "../store/action";

import { getData as getDataCertigicate } from "../../../course/certificate/store/action";

const SidebarNewKind = ({
  open,
  toggleSidebar,
  intl,
  disable,
  avatar,
  setAvatar,
  setDisable,
}) => {
  const product = useSelector((state) => state.products);
  const student = useSelector((state) => state.users);
  const { productDetail } = useSelector((state) => state.user_certificate);
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [changeSelect, setChangeSelect] = useState();
  const [changeCertificate, setChangeCertificate] = useState();
  const [fileImage, setFileImage] = useState();

  const statusOptions = [
    { value: 1, label: <FormattedMessage id="Active" /> },
    { value: 0, label: <FormattedMessage id="Blocked" /> },
  ];
  const [selectStatus, setSelectStatus] = useState({
    value: 1,
    label: <FormattedMessage id="Active" />,
  });

  const lang = useSelector((state) => state.common.language);

  const UserCertificateOptions = validateOptions.UserCertificateOptions;
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(
      getDataCertigicate({
        filter: {
          status: "1",
        },
        // skip: 0,
        // limit: 20,
        order: [
          {
            key: "id",
            value: "asc",
          },
        ],
      })
    );
  }, [lang]);

  useEffect(() => {
    if (changeCertificate) {
      dispatch(
        getById({
          id: parseInt(changeCertificate),

          lang: lang,
        })
      );
    }
  }, [changeCertificate, dispatch, lang]);

  const cate = yup.object({
    productId: yup
      .number()
      .required(<FormattedMessage id="The course field is required" />),
    usersId: yup
      .number()
      .required(
        <FormattedMessage id="Select the student who wants to issue the certificate" />
      ),
    certificateId: yup
      .string()
      .required(<FormattedMessage id="The certifycate field is required" />),
  });
  const {
    register,
    formState: { errors },
    handleSubmit,
    control,
    setError,
    reset,
    setValue,
    clearErrors,
  } = useForm({
    resolver: yupResolver(cate),
    mode: "all",
  });

  const onChange = async (e) => {
    const imageBase64 = await convertFileToBase64(e.target.files[0]);
    setFileImage(e.target.files[0]);
    setAvatar(imageBase64);
  };

  const onSubmit = async (values) => {
    setDisable(true);

    var urlImage = " ";

    if (avatar) {
      const newData = avatar.replace(/,/gi, "").split("base64");
      if (newData[1]) {
        if (newData[1] == BG_BLANK) {
          toast.error(<FormattedMessage id="Image is required" />);
          setDisable(false);
          return;
        }
        const data = {
          imageData: newData[1],
          imageFormat: fileImage?.type?.split("/")[1] || "png",
        };

        await uploadImage(data, fileImage).then((res) => {
          urlImage = res?.data;
        });
      } else {
        urlImage = avatar;
      }

      if (urlImage) {
        dispatch(
          addUserCertificate({
            ...values,
            productId: values.productId,
            certificateId: productDetail?.certificate?.id
              ? productDetail?.certificate?.id
              : "",
            lang: lang,
            stamp: urlImage,
            status: selectStatus.value,
            usersId: values.usersId,
          })
        );
      }
    }
  };

  useEffect(() => {
    if (isSubmitted) {
      reset();
      setIsSubmitted(false);
    }
  }, [isSubmitted, reset]);

  return (
    <Sidebar
      size="lg"
      open={open}
      title={<FormattedMessage id="Add new certificate" />}
      headerClassName="mb-1"
      contentClassName="pt-0"
      toggleSidebar={toggleSidebar}
    >
      <Form onSubmit={handleSubmit(onSubmit)}>
        <FormGroup>
          <Label>
            <FormattedMessage id="Course" />
            <span className="text-danger"> * </span>
          </Label>

          <Controller
            control={control}
            name="productId"
            render={({ field }) => {
              return (
                <Select
                  id="productId"
                  innerRef={register}
                  name="productId"
                  placeholder={<FormattedMessage id="Select Course" />}
                  className={classnames(
                    "react-select",
                    !changeSelect
                      ? {
                          "is-invalid": errors["productId"],
                        }
                      : ""
                  )}
                  options={product?.data?.map((item, index) => {
                    return {
                      value: item?.id && item?.id,
                      label: item.product_names[0].name || "",
                    };
                  })}
                  classNamePrefix="select"
                  {...field}
                  onChange={(e) => {
                    setError("productId", "");
                    setValue("productId", e?.value);
                    setChangeSelect(e?.value);
                    setChangeCertificate(e?.value);
                  }}
                />
              );
            }}
          ></Controller>
          <small className="text-danger">
            {errors?.productId && errors.productId.message}
          </small>
        </FormGroup>
        <FormGroup>
          <Label for="certificateId">
            <FormattedMessage id="certificate" />
            <span className="text-danger"> * </span>
          </Label>
          <Input
            name="certificateId"
            id="certificateId"
            placeholder=""
            value={
              productDetail?.certificate?.id
                ? productDetail?.certificate?.title
                : ""
            }
            readOnly={true}
            innerRef={register(UserCertificateOptions.certificateId)}
            className={
              productDetail?.certificate
                ? ""
                : classnames({ "is-invalid": errors["certificateId"] })
            }
          />

          {productDetail?.certificate == null && (
            <small className="text-danger">
              {errors?.certificateId && errors.certificateId.message}
            </small>
          )}
        </FormGroup>

        <FormGroup>
          <Label>
            <FormattedMessage id="Student" />
            <span className="text-danger"> * </span>
          </Label>
          <Controller
            control={control}
            name="usersId"
            render={({ field }) => {
              return (
                <Select
                  id="usersId"
                  innerRef={register}
                  name="usersId"
                  placeholder={<FormattedMessage id="Select Student" />}
                  className={classnames(
                    "react-select",
                    !changeSelect
                      ? {
                          "is-invalid": errors["usersId"],
                        }
                      : ""
                  )}
                  options={student?.data?.map((item, index) => {
                    return {
                      value: item?.id && item?.id,
                      label: item.first_name + " " + item.last_name || "",
                    };
                  })}
                  classNamePrefix="select"
                  {...field}
                  onChange={(e) => {
                    setError("usersId", "");
                    setValue("usersId", e?.value);
                    setChangeSelect(e?.value);
                  }}
                />
              );
            }}
          ></Controller>
          <small className="text-danger">
            {errors?.usersId && errors.usersId.message}
          </small>
        </FormGroup>

        <FormGroup>
          <Label>
            <FormattedMessage id="status" />
            <span className="text-danger"> * </span>
          </Label>
          <Controller
            control={control}
            name="status"
            render={({ field }) => {
              return (
                <Select
                  id="status"
                  innerRef={register}
                  name="status"
                  placeholder={<FormattedMessage id="Select status" />}
                  options={statusOptions}
                  defaultValue={selectStatus}
                  classNamePrefix="select"
                  {...field}
                  onChange={(e) => {
                    setError("status", "");
                    setValue("status", e?.value);
                    setChangeSelect(e?.value);
                    setSelectStatus(e);
                  }}
                />
              );
            }}
          ></Controller>
        </FormGroup>

        <FormGroup>
          <Label for="Stamp">
            <FormattedMessage id="Photo of seal and signature" />
            <span className="text-danger"> *</span>
          </Label>
          <Row className="align-items-end p-1">
            <Media className="mr-25" left>
              <Media
                object
                className="rounded mr-50"
                src={avatar}
                height="100"
                width="100"
              />
            </Media>{" "}
            <Row className="flex-column px-1">
              <Button.Ripple tag={Label} className="mt-1" color="primary">
                <FormattedMessage id="Upload" />
                <Input
                  type="file"
                  onChange={(e) => onChange(e)}
                  hidden
                  accept="image/*"
                />
              </Button.Ripple>
            </Row>
          </Row>
        </FormGroup>

        <div style={{ textAlign: "end" }}>
          <Button
            type="submit"
            className="mr-1"
            color="primary"
            disabled={disable}
          >
            <FormattedMessage id="add" />
          </Button>
          <Button
            type="reset"
            color="secondary"
            outline
            onClick={toggleSidebar}
          >
            <FormattedMessage id="Cancel" />
          </Button>
        </div>
      </Form>
    </Sidebar>
  );
};

export default injectIntl(SidebarNewKind);
