import ExtensionsHeader from "@components/extensions-header";
import { useEffect, useState } from "react";
import { FormattedMessage, injectIntl } from "react-intl";
import Select from "react-select";
import Sidebar from "./Sidebar";
import { columns } from "./columns";
import { useDispatch, useSelector } from "react-redux";
import { getData, getDataExport } from "../store/action";
import { getData as getDataProduct } from "../../../product/store/action";
import { getData as getDataStudent } from "../../../user/store/action";

import avatarBlank from "./../../../../../assets/images/avatars/bg-blank.png";

import DataTable from "react-data-table-component";
import { ChevronDown } from "react-feather";
import ReactPaginate from "react-paginate";
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  CardTitle,
  Col,
  Input,
  Row,
} from "reactstrap";

import "@styles/react/libs/react-select/_react-select.scss";
import "@styles/react/libs/tables/react-dataTable-component.scss";
import { toast } from "react-toastify";
import XLSX from "xlsx";

const CustomHeader = ({ toggleSidebar, exportToExcel }) => {
  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-1 mb-75">
      <Row className="justify-content-end">
        <Col
          xl="6"
          className="d-flex align-items-sm-center justify-content-lg-end justify-content-start flex-lg-nowrap flex-wrap flex-sm-row flex-column gap-3 pr-lg-1 p-0 mt-lg-0 mt-1"
        >
          <Button.Ripple color="primary" onClick={exportToExcel}>
            Export Excel
          </Button.Ripple>
          <Button.Ripple color="primary" onClick={toggleSidebar}>
            <FormattedMessage id="Add" />
          </Button.Ripple>
        </Col>
      </Row>
    </div>
  );
};

const UserCertificate = ({ intl }) => {
  const dispatch = useDispatch();
  const store = useSelector((state) => state.user_certificate);
  const product = useSelector((state) => state.products);
  const [productOptions, setProductOptions] = useState();
  const student = useSelector((state) => state.users);
  const [studentOptions, setStudentOptions] = useState();
  const [avatar, setAvatar] = useState(avatarBlank);
  const statusOptions = [
    { value: "", label: <FormattedMessage id="Select status" /> },
    // { value: "0", label: <FormattedMessage id="Deactive" /> },
    { value: "1", label: <FormattedMessage id="Active" /> },
    { value: "0", label: <FormattedMessage id="Blocked" /> },
  ];
  const [searchTerm, setSearchTerm] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(20);
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [disable, setDisable] = useState(false);
  const lang = useSelector((state) => state.common.language);
  const [filterStatus, setFilterStatus] = useState({
    value: "",
    label: <FormattedMessage id="status" />,
  });
  const [filterProduct, setFilterProduct] = useState({
    value: "",
    label: <FormattedMessage id="Course" />,
  });
  const [filterStudent, setFilterStudent] = useState({
    value: "",
    label: <FormattedMessage id="Student" />,
  });

  const [searchName, setSearchName] = useState(null);

  const [loadData, setLoadData] = useState(false);
  const toggleSidebar = () => {
    setSidebarOpen(!sidebarOpen);
    setDisable(false);
  };

  useEffect(() => {
    dispatch(
      getData({
        filter: {
          status: filterStatus.value || undefined,
          usersId: filterStudent.value || undefined,
          productId: filterProduct.value || undefined,
          lang,
        },
        skip: (currentPage - 1) * rowsPerPage,
        limit: rowsPerPage,
        order: [
          {
            key: "status",
            value: "desc",
          },
        ],
      })
    );
  }, [loadData, currentPage, rowsPerPage, lang]);

  useEffect(() => {
    dispatch(
      getDataExport({
        filter: {
          lang,
        },
        order: [
          {
            key: "id",
            value: "asc",
          },
        ],
      })
    );
  }, [store?.status, lang]);

  useEffect(() => {
    dispatch(
      getDataProduct({
        filter: {
          status: 1,
          lang,
          product_type: 0,
        },
        // skip: 0,
        // limit: 1000,
        order: [
          {
            key: "id",
            value: "asc",
          },
        ],
      })
    );
  }, [lang]);

  useEffect(() => {
    dispatch(
      getDataStudent({
        filter: {},
        // skip: 0,
        // limit: 20,
        order: {
          key: "id",
          value: "asc",
        },
      })
    );
  }, [lang]);

  useEffect(() => {
    if (product?.data?.length > 0) {
      let data = [];
      data[0] = {
        value: null,
        label: <FormattedMessage id="Select Course" />,
      };
      product?.data?.forEach((item, index) => {
        data[index + 1] = {
          value: item?.id && item?.id,
          label: item.product_names[0].name,
        };
      });
      setProductOptions(data);
    }
  }, [product?.data?.length]);

  useEffect(() => {
    if (student?.data?.length > 0) {
      let data = [];
      data[0] = {
        value: null,
        label: <FormattedMessage id="Select Student" />,
      };
      student?.data?.forEach((item, index) => {
        data[index + 1] = {
          value: item?.id && item?.id,
          label: item.first_name + " " + item.last_name,
        };
      });
      setStudentOptions(data);
    }
  }, [student?.data?.length]);

  useEffect(() => {
    switch (store?.type) {
      case "ADD_USER_CERTIFICATE":
        if (store?.status == 200) {
          toast.success(<FormattedMessage id={"successfully added new!"} />);
          setLoadData(!loadData);
          toggleSidebar();
        } else if (store?.status == 400) {
          toast.warn(<FormattedMessage id={"Add new failure!"} />);
        }
        break;
      case "UPDATE_USER_CERTIFICATE":
        if (store?.status == 200) {
          toast.success(<FormattedMessage id={"Update successful!"} />);
          setLoadData(!loadData);
        } else if (store?.status == 400) {
          toast.warn(<FormattedMessage id={"Update failed!"} />);
        }
        break;
      default:
        break;
    }
  }, [store?.status]);

  const handlePerPage = (e) => {
    const value = parseInt(e.currentTarget.value);
    setRowsPerPage(value);
  };
  const count = Number(Math.ceil(store?.total / rowsPerPage));

  const dataToRender = () => {
    if (store?.data?.length > 0) {
      return store?.data;
    }
  };

  const exportToExcel = () => {
    if (store?.dataExport?.length > 0) {
      const data = store?.dataExport;
      const cols = [
        { header: "ID", key: "id" },
        { header: "Kho hc", key: "product" },
        { header: "Hc vin", key: "user" },
        { header: "Chng ch", key: "certificate" },
        { header: "Qr code", key: "qr_code" },

        { header: "Trng thi", key: "status" },
        { header: "Ngn ng", key: "lang" },
        { header: "Ngy to", key: "createdAt" },
        { header: "Ngy cp nht", key: "updatedAt" },
      ];
      const mappedData = data.map((item) =>
        Object.fromEntries(
          Object.entries(item).map(([key, val]) => {
            if (key === "product" && val) {
              return [
                cols.find((col) => col.key === key)?.header || key,
                val?.product_names?.length > 0 && val?.product_names[0]?.name,
              ];
            }
            if (key === "user" && val) {
              return [
                cols.find((col) => col.key === key)?.header || key,
                val?.first_name &&
                  val?.last_name &&
                  val?.first_name + " " + val?.last_name,
              ];
            }
            if (key === "certificate" && val) {
              return [
                cols.find((col) => col.key === key)?.header || key,
                val?.title && val?.title,
              ];
            }
            return [cols.find((col) => col.key === key)?.header || key, val];
          })
        )
      );
      const worksheet = XLSX.utils.json_to_sheet(mappedData, {
        header: cols.map((col) => col.header),
      });
      const workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, worksheet, "Chng ch hc vin");
      XLSX.writeFile(workbook, "certificate.xlsx");
    }
  };

  return (
    <div className="app-user-list">
      <ExtensionsHeader title={<FormattedMessage id="certificate user" />} />
      <Card>
        <CardBody>
          <Row>
            <Col md="4">
              <Select
                isClearable={false}
                className="react-select"
                classNamePrefix="select"
                options={productOptions}
                value={filterProduct}
                onChange={(data) => {
                  setFilterProduct(data);
                }}
              />
            </Col>

            <Col md="4">
              <Select
                isClearable={false}
                className="react-select"
                classNamePrefix="select"
                options={studentOptions}
                value={filterStudent}
                onChange={(data) => {
                  setFilterStudent(data);
                }}
              />
            </Col>

            <Col md="4">
              <Select
                isClearable={false}
                className="react-select"
                classNamePrefix="select"
                options={statusOptions}
                value={filterStatus}
                onChange={(data) => {
                  setFilterStatus(data);
                }}
              />
            </Col>

            <Col md="4">
              <Button
                color="primary"
                onClick={() => {
                  setLoadData(!loadData);
                  setCurrentPage(1);
                }}
              >
                <FormattedMessage id="Search" />
              </Button>
            </Col>
          </Row>
        </CardBody>
      </Card>
      <Card>
        <DataTable
          noHeader
          subHeader
          responsive
          highlightOnHover
          noDataComponent={
            <div className="sc-fznWqX gnahTY">
              <div className="sc-AxjAm gIMaKV rdt_Table" role="table">
                <div className="sc-fzqARJ icdHOq">
                  <div
                    style={{
                      padding: "25px ",
                      textAlign: "center",
                      color: "black",
                      background: "white",
                    }}
                  >
                    <FormattedMessage id={"There are no records to display"} />
                  </div>
                </div>
              </div>
            </div>
          }
          paginationServer
          columns={columns}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              toggleSidebar={toggleSidebar}
              handlePerPage={handlePerPage}
              rowsPerPage={rowsPerPage}
              searchTerm={searchTerm}
              exportToExcel={exportToExcel}
            />
          }
        />
      </Card>
      {count > 0 && (
        <ReactPaginate
          previousLabel={""}
          nextLabel={""}
          pageCount={count || 0}
          activeClassName="active"
          forcePage={currentPage !== 0 ? currentPage - 1 : 0}
          onPageChange={(page) => setCurrentPage(page.selected + 1)}
          pageClassName={"page-item"}
          nextLinkClassName={"page-link"}
          nextClassName={"page-item next"}
          previousClassName={"page-item prev"}
          previousLinkClassName={"page-link"}
          pageLinkClassName={"page-link"}
          containerClassName={
            "pagination react-paginate justify-content-center my-2 pr-1"
          }
        />
      )}
      <Sidebar
        open={sidebarOpen}
        toggleSidebar={toggleSidebar}
        {...{
          avatar,
          setAvatar,
          disable,
          setDisable,
        }}
      />
    </div>
  );
};

export default injectIntl(UserCertificate);
